@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url(../fonts/oswald/oswaldmedium.woff2);
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/oswald/oswaldregular.woff2);
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/lato/Lato-Regular.woff2);
}
body {
  margin: 0;
  font-family: "Oswald";
  font-style: normal;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  font-family: "Oswald";
  font-style: normal;
  display: block;
  text-transform: uppercase;
  padding: 7px 44px;
  text-align: center;
}
.button--primary {
  background-color: #68B738;
  color: #FFFFFF;
}
.button--secundary {
  background-color: #F2F2F2;
  color: #444444;
}
.main-header {
  padding: 10px 20px 17px;
}
.main-header__logo {
  width: 100%;
}
.main-nav__toggle {
  background-color: transparent;
  border: none;
}
.site-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.site-list__item {
  padding: 22px 0 24px;
}
.site-list__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.promo {
  padding: 20px;
}
.promo__wrapper {
  padding: 27px 20px 20px;
  background-color: rgba(104, 183, 56, 0.85);
}
.promo__headline {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #FFFFFF;
  margin: 0 0 25px;
}
.promo__description {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0 0 29px;
}
.promo__link {
  width: 192px;
  font-size: 14px;
  line-height: 26px;
}
.promo__product-image {
  display: inline-block;
  margin-bottom: 3px;
}
.programs {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding: 0 20px 20px 20px;
}
.programs__item:not(:last-child) {
  margin-bottom: 20px;
}
.programs__item-wrapper {
  background-color: #F2F2F2;
  padding: 21px 20px 18px 19px;
}
.programs__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
  text-transform: uppercase;
}
.programs__description {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  padding: 0 0 23px;
  margin: 0 0 15px;
  border-bottom: 1px #D9D9D9 solid;
}
.programs__link {
  position: relative;
  padding-right: 35px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}
.programs__link::after {
  position: absolute;
  top: 5px;
  left: 97px;
  width: 24px;
  height: 12px;
  background-image: url("../img/icon/icon-arrow.svg");
  background-size: 100%;
  content: "";
}
.advantages {
  padding: 0 20px 20px 20px;
}
.advantages__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.advantages-description {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.live-example {
  padding: 25px 20px 40px;
  background-color: #EAEAEA;
}
.live-example__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin-bottom: 40px;
}
.live-example__description {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  color: #444444;
  margin-bottom: 20px;
}
.live-example-list {
  margin-bottom: 20px;
}
.live-example__objection {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
}
.live-example__result {
  font-size: 24px;
  line-height: 24px;
}
.live-example__wrapper-conlusion {
  margin-bottom: 20px;
}
.live-example__outcome {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}
.live-example__slider {
  padding: 0 20px;
}
.live-example__slider {
  width: 240px;
  height: 256px;
  transform: translate3d(0, 0, 0);
  overflow: hidden;
}
.slider__before,
.slider__after {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-color: #FFFFFF;
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  overflow: hidden;
}
.slider__content-image {
  height: 100%;
}
.slider__before {
  width: 140px;
}
.slider__scroller {
  position: absolute;
  left: 119px;
  top: 50%;
  width: 40px;
  height: 40px;
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-50%);
  border: 2px #666666 solid;
  border-radius: 50%;
  opacity: 0.9;
  pointer-events: auto;
  cursor: pointer;
}
.slider__scroller::before,
.slider__scroller::after {
  background-color: #666666;
  content: " ";
  display: block;
  width: 7px;
  height: 9999px;
  position: absolute;
  left: 50%;
  margin-left: -3px;
  z-index: 30;
  transition: 0.1s;
}
.slider__scroller::before {
  top: 100%;
}
.slider__scroller::after {
  bottom: 100%;
}
.slider__scroller:hover path {
  fill: #68B738;
}
.slider__scroller:hover {
  border-color: #68B738;
}
.slider__scroller:hover:after,
.slider__scroller:hover:before {
  background-color: #68B738;
}
.scrolling {
  pointer-events: none;
  opacity: 1;
  z-index: 1;
}
.scroller__thumb {
  width: 100%;
  height: 100%;
  padding: 5px;
}
.location__address {
  padding: 26px 20px;
}
.main-footer {
  background-color: #F2F2F2;
}
.main-footer__logo {
  padding: 39px 20px 32px;
}
.main-footer__social-list {
  margin: 0;
  padding: 23px 20px;
}
.main-footer__copyright {
  padding: 28px 20px 22px;
}
.contacts {
  padding: 26px 20px;
}
.contacts--light {
  background-color: #FFFFFF;
}
.contacts__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.contacts__description {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}
.social {
  list-style-type: none;
}
.catalog-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  border-top: 1px #D9D9D9 solid;
  border-bottom: 1px #D9D9D9 solid;
  padding: 20px 20px 41px;
  margin: 0;
}
.catalog__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.catalog__item {
  padding: 20px 20px 25px;
  border-bottom: 1px #D9D9D9 solid;
}
.product--show-all {
  padding-top: 78px;
}
.product__link {
  color: #000000;
}
.product__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 18px;
}
.product__detail-list {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 13px;
}
.product__main-link {
  padding: 8px 110px;
}
.extra-catalog__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  margin: 0;
  padding: 25px 20px 36px;
}
.extra-catalog__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding: 0 20px;
}
.extra-catalog__item {
  padding: 13px 0 20px;
  border-bottom: 1px #D9D9D9 solid;
}
.extra-catalog__item:first-child {
  border-top: 1px #D9D9D9 solid;
}
.extra-product__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  margin: 0 0 13px;
}
.extra-product__detail {
  margin: 0 0 15px;
}
.extra-product__amount,
.extra-product__price {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
}
.promotion {
  margin: 35px 20px;
  padding: 59px 61px 43px 59px;
  background-color: rgba(104, 183, 56, 0.85);
  text-align: center;
}
.promotion__description {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}
.main-container--form {
  text-transform: uppercase;
}
.form-headline {
  padding: 27px 41px 20px 20px;
}
.form-headline__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-transform: none;
  margin: 0 0 37px 0;
}
.form-headline__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  margin: 0 0 19px 0;
}
.information-cat__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding: 0 20px 24px;
}
.information-cat__item:not(:last-child) {
  margin-bottom: 14px;
}
.form-item__input-label {
  font-weight: 400;
  color: #444444;
  margin-right: 12px;
}
.form-item__input {
  font-family: "Oswald";
  font-style: normal;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  padding: 9px 18px 8px 18px;
}
.form-item__input::placeholder {
  color: #444444;
}
.control__radio-label,
.control__checkbox-label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #222222;
}
.program-group {
  padding: 0;
  margin-bottom: 26px;
}
.program-group__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding: 27px 25px 20px 20px;
}
.program-group__item:not(:last-child) {
  margin-bottom: 13px;
}
.information-owner__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
}
.information-owner__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.information-owner__item:not(:last-child) {
  margin-bottom: 14px;
}
.comment__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  margin-bottom: 37px;
}
.comment__text {
  border-color: #E7E7E7;
  resize: none;
  width: 100%;
  height: 150px;
}
.comment__text::placeholder {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
}
.additionally {
  margin-bottom: 40px;
}
.additionally__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  margin-bottom: 34px;
}
.additionally__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.additionally__item:not(:last-child) {
  margin-bottom: 13px;
}
.form__submit-button {
  border: none;
  width: 100%;
  font-size: 16px;
  line-height: 26px;
}
.form__information-cat,
.form__program-group {
  padding: 0;
  border: none;
}
.form__information-cat,
.form__program-group {
  border-bottom: 1px #EAEAEA solid;
}
.form__information-owner {
  padding: 26px 20px 23px;
  border: none;
}
.form__comment {
  padding: 0 20px 23px;
  border: none;
}
.form__additionally {
  padding: 0 20px 34px;
  border: none;
}
.form__button-wrapper,
.form__notification {
  padding: 0 20px;
}
